$watch-size: 80vmin;
$watch-border: 7px dotted #3c1111;
$watch-color: #bbc975;
$center-size: 0.07 * $watch-size;
$center-color: #d58d08;
$second-width: $center-size / 5;
$second-height: $watch-size / 2;
$second-color: #d70f73;
$minute-height: $center-size / 3;
$minute-width: $watch-size / 4;
$minute-color: #584141;
